* {
  font-family: 'Roboto' sans-serif; }

nav {
  height: 51px;
  background-color: #212121;
  padding: 13px 70px; }

.menu {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0;
  padding-inline-start: 0;
  list-style-type: none; }

.menu_item {
  padding: 0 38px;
  border-right: 2px dotted black;

  &:last-child {
    border-right: none; } }

.menu_link {
  color: #ffffff;
  font-size: 15px;
  font-weight: 300; }

.subheader {
  position: relative;
  z-index: 1;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6); }

.subheader_logo {
  display: block;
  margin-top: 16px;
  max-width: 100%; }

.subheader_oficcial {
  margin-top: 6px;
  opacity: 0.5;
  color: #ffffff;
  font-size: 13px;
  font-weight: 300; }

.subheader_call {
  margin-top: 23px;
  text-align: right;
  color: #ffffff;
  font-size: 12px;
  font-weight: 300;
  line-height: 14px; }

.subheader-phone {
  display: block;
  margin-top: 8px;
  text-align: right;
  color: #ffffff;
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 18px; }

.subheader_btn {
  margin-top: 19px;
  width: 172px;
  height: 46px;
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
  color: #ffffff;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase; }

.main {
  margin-top: -84px;
  background: url(../img/main-bcg.png) center (center / cover) no-repeat;
  min-height: 800px;
  text-align: center; }

.main_title {
  margin-top: 151px;
  color: #ffffff;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 60px; }

.main_subtitle {
  margin-top: 20px;
  color: #ffffff;
  font-size: 36px;
  text-transform: uppercase;
  font-weight: 700; }

.subheader_discr {
  margin-top: 23px;
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px; }

.main_btn {
  margin-top: 118px;
  width: 259px;
  height: 63px;
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
  color: #ffffff;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase; }

.reasons {
  min-height: 900px;
  padding: 70px 0; }

.label {
  margin: 0 auto;
  padding: 3px 11px;
  width: 112px;
  height: 24px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center; }

.title {
  margin-top: 44px;
  margin-bottom: 0;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center; }

.reasons_discr {
  margin-top: 40px;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.reasons_img {
  margin-right: 26px; }

.reasons_text {
  flex-basis: 70%;
  color: #202020;
  font-size: 15px;
  font-weight: 300;
  line-height: 21px;
  text-align: justify;

  span {
    color: #1eacc7;
    font-size: 17px;
    font-weight: 400; } }

.mobile {
  height: 864px;
  background-color: #1eacc7; }

